/* Font Awesome Brands */
@font-face {
  font-family: "Font Awesome 5 Brands";
  font-style: normal;
  font-weight: 400;
  font-display: block;
  src: url("./components/webfonts/fa-brands-400.eot");
  src: url("./components/webfonts/fa-brands-400.eot?#iefix") format("embedded-opentype"),
       url("./components/webfonts/fa-brands-400.woff2") format("woff2"),
       url("./components/webfonts/fa-brands-400.woff") format("woff"),
       url("./components/webfonts/fa-brands-400.ttf") format("truetype"),
       url("./components/webfonts/fa-brands-400.svg#fontawesome") format("svg");
}

.fab {
  font-family: "Font Awesome 5 Brands";
  font-weight: 400;
}

/* Font Awesome Regular */
@font-face {
  font-family: "Font Awesome 5 Free";
  font-style: normal;
  font-weight: 400;
  font-display: block;
  src: url("./components/webfonts/fa-regular-400.eot");
  src: url("./components/webfonts/fa-regular-400.eot?#iefix") format("embedded-opentype"),
       url("./components/webfonts/fa-regular-400.woff2") format("woff2"),
       url("./components/webfonts/fa-regular-400.woff") format("woff"),
       url("./components/webfonts/fa-regular-400.ttf") format("truetype"),
       url("./components/webfonts/fa-regular-400.svg#fontawesome") format("svg");
}

.far {
  font-family: "Font Awesome 5 Free";
  font-weight: 400;
}

/* Font Awesome Solid */
@font-face {
  font-family: "Font Awesome 5 Free";
  font-style: normal;
  font-weight: 900;
  font-display: block;
  src: url("./components/webfonts/fa-solid-900.eot");
  src: url("./components/webfonts/fa-solid-900.eot?#iefix") format("embedded-opentype"),
       url("./components/webfonts/fa-solid-900.woff2") format("woff2"),
       url("./components/webfonts/fa-solid-900.woff") format("woff"),
       url("./components/webfonts/fa-solid-900.ttf") format("truetype"),
       url("./components/webfonts/fa-solid-900.svg#fontawesome") format("svg");
}

.fa,
.fas {
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
}

/* Base styles for Font Awesome icons */
.fa,
.fas,
.far,
.fab {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  line-height: 1;
}

/* Font Awesome Icon Definitions */
.fa-smile::before {
  content: "\f118";
}
